class Register {
    constructor(response) {
        this.done = response[0] === 1;
        this.error = response[1] === 1;
        this.unkownError = response[2] === 1;
        this.notReady = response[3] === 1;
        this.isEnded = response[4] === 1;
        this.isFull = response[5] === 1;
        this.username_used = response[6] === 1;
        this.email_used = response[7] === 1;
        this.e_username = response[8];
        this.e_password = response[9];
        this.e_email = response[10];
        this.e_race = response[11];
    }
}

class ServerInfo {
    constructor(server = []) {
        this.serverNumber = server[0];
        this.ready = server[1] === 1;
        this.ended = server[2] === 1;
        this.isFull = server[3] === 1;
        this.started = server[4];
        this.players = server[5];

    }
}

class ListServers {
    /** @type {ServerInfo[]} */
    servers = [];
    constructor(servers = []) {
        servers.forEach(server => this.servers.push(new ServerInfo(server)));
    }
}

class Stats {
    constructor(response = []) {
        this.players = response[0];
        this.online = response[1];
        this.servers = response[2];
    }
}

export default { Register, ListServers, Stats }